
@import url("../node_modules/bootstrap-block-grid/dist/bootstrap3-block-grid.min.css");
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #231f20;
}
.text-primary {
	color: #f69471;
}
.text-secondary {
	color: #164371;
}
.alert-primary {
	background: #213B5E;
	color: #fff;
}
.alert-primary small {
	color: #fff;
}
.alert-danger {
	background: #FADDCB;
	color: #231f20;
}
.alert-danger .title {
	color: #213B5E;
}




